import React from 'react';
import { SecondTabs, SecondTable } from '../component/second/Second';
import { BomListTable, KnifeList, KnifeListTable } from '../component/second/Knife';
import { StorageInventory, VendorInventory, OldKnifeInventory } from '../component/second/Inventory';
import Form from 'react-bootstrap/Form';
import './pages.css';

class Second extends React.Component {
  constructor() {
    super();
    this.showModal = this.showModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.state = {
      modal_tips: false
    }
  }

  showModal(e) {
    this.setState({
      [e.target.getAttribute("modaltoshow")]: true
    });
  }

  closeModal() {
    this.setState({
      modal_tips: false
    });
  }

  render() {
    return (
      <React.Fragment>
        <div className="sys_area second_area clearfix">
          <SecondTabs tab='1' />
          <div className="tab-content tabs_content shadow-lg clearfix">
            <div className="d-flex justify-content-start clear_both">
              <div className="clearfix">
                <div className="search_area clearfix float-left">
                  <div className="input-group">
                    <input type="text" className="ipt_search form-control" placeholder="請輸入工單或OP..." />
                    <div className="input-group-append">
                      <button type="button" className="btn_search d-flex"><i className="icon-search"></i></button>
                    </div>                  
                  </div>
                </div>
              </div>
            </div>
            <hr />
            <div className="work_area clearfix">
              <SecondTable />
            </div>
            <div className="d-flex justify-content-xl-end justify-content-md-center">
              <nav className="pagination_area clearfix">
                <ul className="pagination mb-0">
                  <li className="page-item disabled"><a className="page-link" href="#/">上一頁</a></li>
                  <li className="page-item active"><a className="page-link" href="#/">1</a></li>
                  <li className="page-item"><a className="page-link" href="#/">2</a></li>
                  <li className="page-item"><a className="page-link" href="#/">3</a></li>
                  <li className="page-item"><a className="page-link" href="#/">下一頁</a></li>
                </ul>
              </nav>
            </div>
          </div>
        </div>
      </React.Fragment>
    )
  }
}

class SecondKnife extends React.Component {
  constructor() {
    super();
    this.showModal = this.showModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.handleSelect = this.handleSelect.bind(this);
    this.state = {
      modal_tips: false,
      select_op: ["001", "001"]
    }
  }

  showModal(e) {
    this.setState({
      [e.target.getAttribute("modaltoshow")]: true
    });
  }

  closeModal() {
    this.setState({
      modal_tips: false
    });
  }

  handleSelect(e) {
    this.setState({
      [e.target.getAttribute("selecttohandle")]: [e.target.value, e.target.options[e.target.selectedIndex].text]
    });
  }

  render() {
    return (
      <React.Fragment>
        <div className="sys_area second_area clearfix">
          <SecondTabs tab='2' />
          <div className="tab-content tabs_content shadow-lg clearfix">
            <div className="row">
              <div className="col-lg-3">
                <h4 className="title_sub_area v_hidden"><i className="icon-play_circle_filled"></i><span>指定刀具清單</span></h4>
                <ul className="list-unstyled mb-0 clearfix second_left_ul">
                  <li className="list-group-item">
                    <div className="form-group">
                      <label htmlFor="" className="col-form-label"><b>工單</b></label>
                      <div>
                        <input type="text" className="form-control" defaultValue="531NK-191000123_L002"/>
                      </div>
                    </div>
                  </li>
                  <li className="list-group-item product_item">
                    <div className="row">
                      <div className="col-md-4">
                        <span className="item_photo" style={{ backgroundImage: "url(http://img.wxcha.com/file/201809/14/1e0cea31d9.jpeg)" }}></span>
                      </div>
                      <div className="col-md-8">
                        <span className="item_text">產品：MAN-A1811036</span>
                        <span className="item_text">ID：ABC-124</span>
                        <span className="item_text">數量：32</span>
                        <span className="item_text">量測標準：1</span>
                      </div>
                    </div>
                  </li>
                  <li className="list-group-item">
                    <div className="row">
                      <div className="col-md-6">
                        <div className="form-group">
                          <label htmlFor="" className="col-form-label"><b>OP</b></label>
                          <Form.Group className="form_select_block">
                            <Form.Control as="select" className="form_select js_select_change" selecttohandle="select_op" onChange={this.handleSelect} value={this.state.select_op[0]}>
                              <option value="001">001</option>
                              <option value="002">002</option>
                              <option value="003">003</option>
                              <option value="004">004</option>
                              <option value="005">005</option>
                              <option value="006">006</option>
                              <option value="007">007</option>
                              <option value="008">008</option>
                            </Form.Control>
                            <span className="form_select_bind">{this.state.select_op[1]}</span>
                          </Form.Group>
                        </div>
                      </div>
                      <div className="col-md-6">
                        <div className="form-group">
                          <label htmlFor="" className="col-form-label"><b>推車ID</b></label>
                          <div>
                            <input type="text" className="form-control" defaultValue="Car001" />
                          </div>
                        </div>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
              <div className="col-lg-6">
                <h4 className="title_sub_area"><i className="icon-play_circle_filled"></i><span>指定產品BOM單</span></h4>
                <div className="work_area clearfix fixed_height">
                  <BomListTable />
                </div>
              </div>
              <div className="col-lg-3">
                <h4 className="title_sub_area"><i className="icon-play_circle_filled"></i><span>指定刀具清單</span></h4>
                <KnifeList />
              </div>
            </div>
            <hr />
            <div className="row">
              <div className="col-lg-9 offset-lg-3">
                <div className="d-flex justify-content-between clear_both">
                  <div className="clearfix">
                    <div className="search_area clearfix float-left">
                      <div className="input-group">
                        <input type="text" className="ipt_search form-control" placeholder="請掃描刀桿ID..." />
                      </div>
                    </div>
                    <button className="btn btn_common float-left"><span>產出刀具清單</span></button>
                  </div>
                  <div className="clearfix">
                    <div className="search_area clearfix float-left">
                      <div className="input-group">
                        <input type="text" className="ipt_search form-control" placeholder="請輸入機台編號..." />
                      </div>
                    </div>
                    <button className="btn btn_common float-left"><span>指派機台</span></button>
                  </div>
                </div>
                <hr />
                <div className="work_area clearfix fixed_height">
                  <KnifeListTable />
                </div>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    )
  }
}

class SecondInventory extends React.Component {
  constructor() {
    super();
    this.showModal = this.showModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.state = {
      modal_tips: false
    }
  }

  showModal(e) {
    this.setState({
      [e.target.getAttribute("modaltoshow")]: true
    });
  }

  closeModal() {
    this.setState({
      modal_tips: false
    });
  }

  render() {
    return (
      <React.Fragment>
        <div className="analysis_area second_area clearfix">
          <SecondTabs tab='3' />
          <div className="tab-content tabs_content shadow-lg clearfix">
            <hr />
            <div className="sort_area">
              <div className="d-flex justify-content-start">
                <div className="list_group">
                  <div className="button_group clearfix">
                    <span>
                      <input id="all_sort" className="ipt_group d-none" type="radio" name="data_sort" defaultChecked />
                      <label className="lab_group m-0" htmlFor="all_sort">全部</label>
                    </span>
                    <span>
                      <input id="exhausted_sort" className="ipt_group d-none" type="radio" name="data_sort" />
                      <label className="lab_group m-0" htmlFor="exhausted_sort">即將耗盡</label>
                    </span>
                  </div>
                </div>
              </div>
            </div>
            <div className="analysis_machine clearfix">
              <div className="analysis_ptqty analysis_same border clearfix">
                <h4 className="title_area mt-0"><b>倉庫庫存（資料來源：ERP）</b></h4>
                <StorageInventory />
              </div>
              <div className="analysis_ptqty analysis_same border clearfix">
                <h4 className="title_area mt-0"><b>販賣機庫存</b></h4>
                <VendorInventory />
              </div>
              <div className="analysis_ptqty analysis_same border clearfix">
                <h4 className="title_area mt-0"><b>舊刀庫存</b></h4>
                <OldKnifeInventory />
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    )
  }
}

export { Second, SecondKnife, SecondInventory };