import React from 'react';
import { Link } from 'react-router-dom';
import { Tabs, Table } from '../Component';

class SecondTabs extends React.Component {
  getClass(tab) {
    if (this.props.tab === tab) {
      return "active";
    } else {
      return "";
    }
  }

  render() {
    return (
      <Tabs>
        <li className="nav-item">
          <Link to="/pages/second" className={"nav-link " + this.getClass('1')}>工單清單</Link>
        </li>
        <li className="nav-item">
          <Link to="/pages/second_knife" className={"nav-link " + this.getClass('2')}>領刀</Link>
        </li>
        <li className="nav-item">
          <Link to="/pages/second_inventory" className={"nav-link " + this.getClass('3')}>刀具庫存</Link>
        </li>
      </Tabs>
    )
  }
}

