import React from 'react';
import { Link } from 'react-router-dom';
import { Tabs, Table} from '../Component';

const $ = require('jquery');
$.DataTable = require('datatables.net');

class IndexTabs extends React.Component {
  getClass(tab) {
    if (this.props.tab === tab) {
      return "active";
    } else {
      return "";
    }
  }

  render() {
    return (
      <Tabs>
        <li className="nav-item">
          <Link to="/pages/index" className={"nav-link " + this.getClass('1')}>機台清單</Link>
        </li>
        <li className="nav-item">
          <Link to="/pages/index_knifebar" className={"nav-link " + this.getClass('2')}>刀桿清單</Link>
        </li>
        <li className="nav-item">
          <Link to="/pages/index_knifemold" className={"nav-link " + this.getClass('3')}>刀具清單</Link>
        </li>
        <li className="nav-item">
          <Link to="/pages/index_bom" className={"nav-link " + this.getClass('4')}>BOM</Link>
        </li>
      </Tabs>
    )
  }
}

