import React from 'react';
import { IndexTabs, IndexTable} from '../component/index/Index';
import { KnifeMoldTable } from '../component/index/KnifeMold';
import { KnifeBarTable } from '../component/index/KnifeBar';
import { BomTable } from '../component/index/Bom';
import Modal from 'react-bootstrap/Modal';
import './pages.css';

class Index extends React.Component{
	constructor() {
		super();
		this.showModal = this.showModal.bind(this);
		this.closeModal = this.closeModal.bind(this);
		this.state = {
			modal_tips: false
		}
	}

	showModal(e) {
		this.setState({
			[e.target.getAttribute("modaltoshow")]: true
		});
	}

	closeModal() {
		this.setState({
			modal_tips: false
		});
	}

  render(){
    return (
      <React.Fragment>
        <div className="sys_area clearfix">
          <IndexTabs tab='1'/>
          <div className="tab-content tabs_content shadow-lg clearfix">
						<div className="d-flex justify-content-end clear_both">
							<div className="clearfix">
								<div className="search_area clearfix float-left">
									<div className="input-group">
										<input type="text" className="ipt_search form-control" placeholder="請輸入機台編號..." />
									</div>
								</div>
								<button className="btn btn_common float-left" modaltoshow="modal_user"><i className="float-left icon-add"></i><span>新增機台</span></button>
							</div>
						</div>
						<hr />
						<div className="work_area clearfix">
							<IndexTable showModal={this.showModal}/>
						</div>
						<div className="d-flex justify-content-xl-end justify-content-md-center">
							<nav className="pagination_area clearfix">
								<ul className="pagination mb-0">
									<li className="page-item disabled"><a className="page-link" href="#/">上一頁</a></li>
									<li className="page-item active"><a className="page-link" href="#/">1</a></li>
									<li className="page-item"><a className="page-link" href="#/">2</a></li>
									<li className="page-item"><a className="page-link" href="#/">3</a></li>
									<li className="page-item"><a className="page-link" href="#/">下一頁</a></li>
								</ul>
							</nav>
						</div>
          </div>
        </div>
				<Modal className="modal_style fade" size="lg" centered show={this.state.modal_tips} onHide={this.closeModal}>
					<Modal.Header closeButton>
						<Modal.Title as="h5"><b>確認訊息</b></Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<h6 className="m-0">您確定要刪除機台<b>CNC03</b>嗎？</h6>
					</Modal.Body>
					<Modal.Footer className="d-flex justify-content-end">
						<button type="button" className="btn btn_common btn_add bg-danger">刪除</button>
						<button type="button" className="btn btn_common btn_cancel" onClick={this.closeModal}>取消</button>
					</Modal.Footer>
				</Modal>
      </React.Fragment>
    )
  }
}

class IndexKnifeBar extends React.Component {
	constructor() {
		super();
		this.showModal = this.showModal.bind(this);
		this.closeModal = this.closeModal.bind(this);
		this.toggleInput = this.toggleInput.bind(this);
		this.state = {
			modal_tips: false,
			modal_fix: false,
			modal_fix_disable: true
		}
	}

	showModal(e) {
		this.setState({
			[e.target.getAttribute("modaltoshow")]: true
		});
	}

	closeModal() {
		this.setState({
			modal_tips: false,
			modal_fix: false
		});
	}

	toggleInput(){
		this.setState({
			modal_fix_disable: !this.state.modal_fix_disable
		});
	}

	render() {
		return (
			<React.Fragment>
				<div className="sys_area clearfix">
					<IndexTabs tab='2' />
					<div className="tab-content tabs_content shadow-lg clearfix">
						<div className="d-flex justify-content-between clear_both">
							<div className="search_area clearfix float-left">
								<div className="input-group">
									<input type="text" className="ipt_search form-control" placeholder="請輸入關鍵字..." />
									<div className="input-group-append">
										<button type="button" className="btn_search d-flex"><i className="icon-search"></i></button>
									</div>
								</div>
							</div>
							<div className="clearfix">
								<div className="search_area clearfix float-left">
									<div className="input-group">
										<input type="text" className="ipt_search form-control" placeholder="請輸入機台編號..." />
									</div>
								</div>
								<button className="btn btn_common float-left" modaltoshow="modal_user"><i className="float-left icon-add"></i><span>建立刀桿</span></button>
								<div className="search_area clearfix float-left">
									<div className="input-group">
										<input type="text" className="ipt_search form-control" placeholder="請輸入機台編號..." />
									</div>
								</div>
								<button className="btn btn_common float-left" modaltoshow="modal_user"><i className="float-left icon-add"></i><span>建立刀桿</span></button>
							</div>
						</div>
						<hr />
						<div className="work_area clearfix">
							<KnifeBarTable showModal={this.showModal}/>
						</div>
						<div className="d-flex justify-content-xl-end justify-content-md-center">
							<nav className="pagination_area clearfix">
								<ul className="pagination mb-0">
									<li className="page-item disabled"><a className="page-link" href="#/">上一頁</a></li>
									<li className="page-item active"><a className="page-link" href="#/">1</a></li>
									<li className="page-item"><a className="page-link" href="#/">2</a></li>
									<li className="page-item"><a className="page-link" href="#/">3</a></li>
									<li className="page-item"><a className="page-link" href="#/">下一頁</a></li>
								</ul>
							</nav>
						</div>
					</div>
				</div>
				<Modal className="modal_style modal_fix fade" size="lg" centered show={this.state.modal_fix} onHide={this.closeModal}>
					<Modal.Header closeButton>
						<Modal.Title as="h5"><b>修正量</b></Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<form>
							<div className="row">
								<div className="col-md-6">
									<div className="form-group row">
										<label htmlFor="" className="col-lg-3 col-md-4 col-form-label text-center"><b>刀桿ID</b></label>
										<div className="col-lg-9 col-md-8">
											<input type="text" className="form-control" defaultValue="A001-002" disabled={true}/>							
										</div>							
									</div>
									<div className="form-group row">
										<label htmlFor="" className="col-lg-3 col-md-4 col-form-label"><b>刀長</b></label>
										<div className="col-lg-9 col-md-8">
											<input type="text" className="form-control" defaultValue="45" disabled={this.state.modal_fix_disable}/>
											<label htmlFor="" className="col-form-label"><b>mm</b></label>
										</div>
									</div>
									<div className="form-group last row">
										<label htmlFor="" className="col-lg-3 col-md-4 col-form-label"><b>刀徑</b></label>
										<div className="col-lg-9 col-md-8">
											<input type="text" className="form-control" defaultValue="14" disabled={this.state.modal_fix_disable}/>
											<label htmlFor="" className="col-form-label"><b>mm</b></label>
										</div>
									</div>
								</div>
								<div className="col-md-6">
									<div className="form-group row">
										<label htmlFor="" className="col-lg-3 col-md-4 col-form-label"><b>修正量1</b></label>
										<div className="col-lg-9 col-md-8">
											<input type="text" className="form-control" defaultValue="3.17" disabled={this.state.modal_fix_disable}/>
											<label htmlFor="" className="col-form-label"><b>mm</b></label>
										</div>
									</div>
									<div className="form-group row">
										<label htmlFor="" className="col-lg-3 col-md-4 col-form-label"><b>修正量2</b></label>
										<div className="col-lg-9 col-md-8">
											<input type="text" className="form-control" defaultValue="0.05" disabled={this.state.modal_fix_disable}/>
											<label htmlFor="" className="col-form-label"><b>mm</b></label>
										</div>
									</div>
									<div className="form-group last row">
										<label htmlFor="" className="col-lg-3 col-md-4 col-form-label"><b>修正量3</b></label>
										<div className="col-lg-9 col-md-8">
											<input type="text" className="form-control" defaultValue="1.2" disabled={this.state.modal_fix_disable}/>
											<label htmlFor="" className="col-form-label"><b>mm</b></label>
										</div>
									</div>
								</div>
							</div>
						</form>
					</Modal.Body>
					<Modal.Footer className="d-flex justify-content-end">
						{(this.state.modal_fix_disable ? 
							<button type="button" className="btn btn_common btn_add" onClick={this.toggleInput}>編輯</button> 
						: 
							<React.Fragment>
								<button type="button" className="btn btn_common btn_add">確認</button> 	
								<button type="button" className="btn btn_common btn_cancel" onClick={this.toggleInput}>取消</button> 	
							</React.Fragment>
						)}
					</Modal.Footer>
				</Modal>
			</React.Fragment>
		)
	}
}

class IndexKnifeMold extends React.Component {
		constructor() {
		super();
		this.showModal = this.showModal.bind(this);
		this.closeModal = this.closeModal.bind(this);
		this.state = {
			modal_takeoff: false
		}
	}

	showModal(e) {
		this.setState({
			[e.target.getAttribute("modaltoshow")]: true
		});
	}

	closeModal() {
		this.setState({
			modal_takeoff: false
		});
	}

	render() {
		return (
			<React.Fragment>
				<div className="sys_area clearfix">
					<IndexTabs tab='3' />
					<div className="tab-content tabs_content shadow-lg clearfix">
						<div className="row">
							<div className="col-lg-4">
								<ul className="list-unstyled mb-0 clearfix">
									<li className="list-group-item">
										<div className="form-group">
											<label htmlFor="" className="col-form-label"><b>刀具形式</b></label>
											<div>
												<input type="text" className="form-control"/>
											</div>
										</div>
										<div className="form-group">
											<label htmlFor="" className="col-form-label"><b>位置</b></label>
											<div>
												<input type="text" className="form-control"/>
											</div>
										</div>
										<div className="form-group">
											<label htmlFor="" className="col-form-label"><b>數量</b></label>
											<div>
												<input type="text" className="form-control"/>
											</div>
										</div>
										<div className="form-group">
											<label htmlFor="" className="col-form-label"><b>滿載數量</b></label>
											<div>
												<input type="text" className="form-control"/>
											</div>
										</div>
										<div className="d-flex justify-content-end clear_both">
											<button className="btn btn_common"><i className="float-left icon-search"></i><span>搜尋</span></button>
											<button className="btn btn_common"><i className="float-left icon-add"></i><span>補充刀具</span></button>
										</div>
									</li>
								</ul>
							</div>
							<div className="col-lg-8">
								<div className="work_area clearfix">
									<KnifeMoldTable showModal={this.showModal}/>
								</div>
								<div className="d-flex justify-content-xl-end justify-content-md-center">
									<nav className="pagination_area clearfix">
										<ul className="pagination mb-0">
											<li className="page-item disabled"><a className="page-link" href="#/">上一頁</a></li>
											<li className="page-item active"><a className="page-link" href="#/">1</a></li>
											<li className="page-item"><a className="page-link" href="#/">2</a></li>
											<li className="page-item"><a className="page-link" href="#/">3</a></li>
											<li className="page-item"><a className="page-link" href="#/">下一頁</a></li>
										</ul>
									</nav>
								</div>
							</div>
						</div>
					</div>
				</div>
			</React.Fragment>
		)
	}
}

