import React from 'react';
import { Link } from 'react-router-dom';
import { useTable, useSortBy } from 'react-table'
import './component.css';

class Frame extends React.Component {
  constructor() {
    super();
    this.handleMouseEnter = this.handleMouseEnter.bind(this);
    this.handleMouseLeave = this.handleMouseLeave.bind(this);
    this.handleMenuClick = this.handleMenuClick.bind(this);
    this.state = {
      sidebarHover: false,
      menuClick: false
    }
  }

  handleMenuClick() {
    this.setState({
      menuClick: !this.state.menuClick
    });
    this.props.handleMenuClick();
  }

  handleMouseEnter() {
    this.setState({
      sidebarHover: true
    })
  }

  handleMouseLeave() {
    this.setState({
      sidebarHover: false
    })
  }

  render() {
    return (
      <React.Fragment>
        <Header sidebarHover={this.state.sidebarHover} menuClick={this.state.menuClick} handleMenuClick={this.handleMenuClick}></Header>
        <SideNav
          currentpage={this.props.currentpage}
          sidebarHover={this.state.sidebarHover}
          menuClick={this.state.menuClick}
          onMouseEnter={this.handleMouseEnter}
          onMouseLeave={this.handleMouseLeave}>
        </SideNav>
      </React.Fragment>
    )
  }
}

class Header extends React.Component {
  constructor() {
    super();
    this.toggleFullScreen = this.toggleFullScreen.bind(this);
    this.showModal = this.showModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.state = {
      fullScreen: false,
      modal_password: false,
      modal_account: false
    }
  }

  showModal(e) {
    this.setState({
      [e.target.getAttribute("modaltoshow")]: true
    });
  }

  closeModal() {
    this.setState({
      modal_password: false,
      modal_account: false
    });
  }

  toggleFullScreen() {
    (document.fullScreenElement && null !== document.fullScreenElement) || (!document.mozFullScreen && !document.webkitIsFullScreen) ? document.documentElement.requestFullScreen ? document.documentElement.requestFullScreen() : document.documentElement.mozRequestFullScreen ? document.documentElement.mozRequestFullScreen() : document.documentElement.webkitRequestFullScreen && document.documentElement.webkitRequestFullScreen(Element.ALLOW_KEYBOARD_INPUT) : document.cancelFullScreen ? document.cancelFullScreen() : document.mozCancelFullScreen ? document.mozCancelFullScreen() : document.webkitCancelFullScreen && document.webkitCancelFullScreen();
    this.setState({
      fullScreen: !this.state.fullScreen
    })
  }

  render() {
    return (
      <React.Fragment>
        <header className="header_area clearfix">
          <div className="float-left">
            <button id="js_menu" className={"btn_menu float-left " + (this.props.sidebarHover || this.props.menuClick ? 'active' : '')} onClick={this.props.handleMenuClick}><i className="icon-menu"></i></button>
            <button className="logo_area float-left"><img src="./images/vcastLogo2@3x.png" height="30" alt="" /></button>
          </div>
        </header>
      </React.Fragment>
    )
  }
}

class SideNav extends React.Component {
  constructor() {
    super();
    this.handlePageChange = this.handlePageChange.bind(this);
    this.getClass = this.getClass.bind(this);
    this.state = {
      currentpage: '1'
    }
  }

  handlePageChange(page) {
    this.setState({
      currentpage: page
    })
  }

  getClass(page) {
    if (this.state.currentpage === page) {
      return "active";
    } else {
      return "";
    }
  }

  render() {
    return (
      <nav className={"sidebar_area js_sidebar clearfix " + (this.props.sidebarHover || this.props.menuClick ? 'hovered' : '')} onMouseEnter={this.props.onMouseEnter} onMouseLeave={this.props.onMouseLeave}>
        <ul className="sidebar_list list-unstyled mb-0">
          <li><Link to="/pages/index" className={this.getClass('1')} onClick={() => this.handlePageChange('1')}><i className="icon-home"></i><span>第一主功能</span></Link></li>
          <li><Link to="/pages/second" className={this.getClass('2')} onClick={() => this.handlePageChange('2')}><i className="icon-equalizer"></i><span>第二主功能</span></Link></li>
        </ul>
      </nav>
    )
  } 
}

class Tabs extends React.PureComponent {
  render() {
    return (
      <ul className="nav nav-tabs tabs_area clearfix">
        {this.props.children}
      </ul>
    )
  }
}

function Table({ columns, data, tableOption }) {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = useTable(
    {
      columns,
      data,
    },
    useSortBy
  )
  let editor = false;
  let tfoot = false;
  if (tableOption) {
    if (tableOption.editorHtml) editor = true;
    if (tableOption.tfootHtml) tfoot = true;
  }
  return (
    <>
      <table {...getTableProps()} className="table table-striped mb-0">
        <thead>
          {headerGroups.map(headerGroup => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column, index) => {                
                return(
                // Add the sorting props to control sorting. For this example
                // we can add them into the header props
                  <th {...column.getHeaderProps(column.getSortByToggleProps())} className={"th_style " + (index === 0 ? "" : "text-center ") + ((column.extraClass) ? column.extraClass : "")}>
                  {/* Add a sort direction indicator */}
                  <span>
                    {column.render('Header')}
                    {column.isSorted
                      ? column.isSortedDesc
                        ? <span className="th_sort sort_desc"></span>
                        : <span className="th_sort sort_asc"></span>
                      : <span className={(column.canSort) ? "th_sort" : "th_style"}></span>}
                  </span>
                </th>
                )
              })}
              {(editor) ? tableOption.editorHtml[0] : null}
            </tr>
          ))}
        </thead>
        {(tfoot) ? tableOption.tfootHtml[0] : null}
        <tbody {...getTableBodyProps()}>
          {rows.map(
            (row, i) => {
              prepareRow(row);
              return (
                <tr {...row.getRowProps()}>
                  {row.cells.map((cell, index) => {
                    return (
                      <td {...cell.getCellProps()} className={(index === 0 ? "" : "text-center ") + ((cell.column.extraClass) ? cell.column.extraClass : "")}>{cell.render('Cell')}</td>
                    )
                  })}
                  {(editor) ? tableOption.editorHtml[1] : null}
                </tr>
              )
            }
          )}
        </tbody>
      </table>
    </>
  )
}

export { Frame, Tabs, Table };
