import React from 'react';
import { Route , Link } from 'react-router-dom';
import { Pages } from './pages/pages';
import Form from 'react-bootstrap/Form';

class App extends React.Component{
  render(){
    return (
      <div className="wrapper_area">
        <Route path="/" exact component={Login} />
        <Route path="/pages" component={Pages} />
      </div>
    );
  }
}

class Login extends React.Component{
  render() {
    return (
      <div className="wrapper_area login_area">
        <main className="main_area clearfix">
          <div className="login_content clearfix">
            <div className="login_bg d-flex">
              <div className="login_left_logo d-flex align-items-center justify-content-center">
                <img className="login_logo" src="./images/vcastLogo2@3x.png" height="50" alt=""/>
              </div>
              <div className="login_inner clearfix">
                <div className="login_form_bg d-flex align-items-center justify-content-center">
                  <div className="login_form_block w-100">
                  <Form className="login-form" action="">
                    <Form.Group>
                      <h3>使用者登入</h3>
                    </Form.Group>
                    <Form.Group controlId="login_email">
                      <Form.Label className="lab_title">電子郵件</Form.Label>
                      <Form.Control type="email" className="ipt_data"/>
                      <div className="help_block d-none"><i className="float-left icon-error_outline"></i><span>此為必填欄位。</span></div>
                    </Form.Group>
                    <Form.Group controlId="login_password">
                      <Form.Label className="lab_title">密碼</Form.Label>
                      <Form.Control type="password" className="ipt_data"/>
                      <div className="help_block d-none"><i className="float-left icon-error_outline"></i><span>此為必填欄位。</span></div>
                    </Form.Group>
                    <Form.Group controlId="keep_login">
                      <div className="checkbox_area clearfix">
                        <input type="checkbox" className="form-check-input d-none" id="keep_login" />
                        <label className="form-check-label" htmlFor="keep_login"><b>保持登入</b></label>
                      </div>
                    </Form.Group>
                    <Form.Group>
                      <Link to="/pages"><button type="button" className="btn btn_submit text-center w-100"><i className="icon-httpslock"></i><span>登入</span></button></Link>
                    </Form.Group>
                  </Form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </main>
      </div>
    );
  }
}

export default App;
