import React from 'react';
import { Route , Switch } from 'react-router-dom';
import { Frame } from '../component/Component';
import { Index, IndexKnifeBar, IndexKnifeMold, IndexBom } from './index';
import { Second, SecondKnife, SecondInventory } from './second';

class Pages extends React.Component{
  constructor() {
    super();
    this.handleMenuClick = this.handleMenuClick.bind(this);
    this.state = {
      menuClick: false
    }
  }

  handleMenuClick(){
    this.setState({
      menuClick: !this.state.menuClick
    })
  }

  render(){
    return (
      <div>
        <Frame handleMenuClick={this.handleMenuClick}></Frame>
        <main className={"main_area js_main clearfix " + (this.state.menuClick ? 'active' : '')}>
          <Switch>
            <Route path="/pages/" exact component={Index} />
            <Route path="/pages/index" component={Index} />
            <Route path="/pages/index_knifebar" component={IndexKnifeBar} />
            <Route path="/pages/index_knifemold" component={IndexKnifeMold} />
            <Route path="/pages/index_bom" component={IndexBom} />
            <Route path="/pages/second" component={Second} />
            <Route path="/pages/second_knife" component={SecondKnife} />
            <Route path="/pages/second_inventory" component={SecondInventory} />
          </Switch>
        </main>
      </div>
    );
  }
}

export {Pages};
