import React from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';

class StorageInventory extends React.Component {
  render() {
    const data = [91, 69, 91, 91, 91, 90, 91, 91, 91, 91, 90, 91, 91, 90, 91, 91, 91, 90, 91, 91, 91, 91, 90, 90, 90];
    const options = {
      chart: {
        type: 'column',
        height: 250,
        marginTop: 0,
        marginRight: 0,
        marginBottom: 45,
        marginLeft: 0,
      },
      colors: ['#97B9FF'],
      credits: {
        enabled: false
      },
      title: {
        text: null
      },
      subtitle: {
        text: null
      },
      xAxis: {
        type: 'datetime',
        tickInterval: 3600 * 1000,
        allowDecimals: false,
        tickWidth: 1,
        labels: {
          style: {
            fontSize: '12px',
            color: '#aaa',
          },
          formatter: function () {
            return Highcharts.dateFormat('%k %p', this.value);
          }
        }
      },
      yAxis: {
        visible: false
      },
      tooltip: {
        enabled: true
      },
      legend: {
        enabled: false
      },
      plotOptions: {
        series: {
          pointInterval: 3600 * 1000
        }
      },
      navigation: {
        buttonOptions: {
          enabled: false
        }
      },
      series: [{
        name: 'ProductCount',
        data: data,
        dataLabels: {
          enabled: true,
          rotation: 0,
          color: '#4F86F2',
          align: 'center',
          y: 0,
          style: {
            fontSize: '12px',
            fontFamily: 'Arial',
            textOutline: 'none'
          }
        }
      }]
    }

    return (
      <HighchartsReact
        highcharts={Highcharts}
        options={options}
      />
    )
  }
}

